import React, { FC } from "react";
import { graphql, PageProps } from "gatsby";
import { Divider, Heading, SimpleGrid, Text } from "@chakra-ui/react";

import { Layout, Link, LinkCard, Seo } from "../components";

const IndexPage: FC<PageProps<Queries.IndexPageQuery>> = ({ data }) => {
  const blogPosts = data.blog.nodes;
  const projects = data.projects.nodes;

  return (
    <Layout title="Home">
      <Seo title="Home" />

      <Heading as="h2" mb={4}>
        <Link to="/projects">Projects</Link>
      </Heading>
      <SimpleGrid columns={{ base: 1, md: 2, lg: 3 }} spacing={4}>
        {projects.map((project) => {
          const title = project.frontmatter?.title || project.fields?.slug;

          return (
            <LinkCard
              key={project.id}
              title={title}
              to={project.fields?.slug || ""}
              image={
                project.frontmatter?.thumbnailImage?.childImageSharp?.gatsbyImageData ||
                project.frontmatter?.featuredImage?.childImageSharp?.gatsbyImageData
              }
            >
              <Text
                dangerouslySetInnerHTML={{
                  __html: project.frontmatter?.description || project.excerpt || "",
                }}
                itemProp="description"
              />
            </LinkCard>
          );
        })}
      </SimpleGrid>

      <Divider my={8} />

      <Heading as="h2" mb={4}>
        <Link to="/blog">Blog</Link>
      </Heading>
      <SimpleGrid columns={{ base: 1, md: 2, lg: 3 }} spacing={4}>
        {blogPosts.map((post) => {
          const title = post.frontmatter?.title || post.fields?.slug;

          return (
            <LinkCard
              key={post.id}
              title={title}
              to={post.fields?.slug || ""}
              footer={post.frontmatter?.date}
              image={
                post.frontmatter?.thumbnailImage?.childImageSharp?.gatsbyImageData ||
                post.frontmatter?.featuredImage?.childImageSharp?.gatsbyImageData
              }
            >
              <Text
                dangerouslySetInnerHTML={{
                  __html: post.frontmatter?.description || post.excerpt || "",
                }}
                itemProp="description"
              />
            </LinkCard>
          );
        })}
      </SimpleGrid>
    </Layout>
  );
};

export default IndexPage;

export const pageQuery = graphql`
  query IndexPage {
    blog: allMdx(sort: { frontmatter: { date: DESC } }, limit: 4, filter: { internal: { contentFilePath: { regex: "//content/blog/.+/" } } }) {
      nodes {
        id
        excerpt
        fields {
          slug
        }
        frontmatter {
          date(formatString: "MMMM DD, YYYY")
          title
          description
          featuredImage {
            childImageSharp {
              gatsbyImageData(width: 300)
            }
          }
          thumbnailImage {
            childImageSharp {
              gatsbyImageData(width: 300)
            }
          }
        }
      }
    }
    projects: allMdx(
      sort: { frontmatter: { title: ASC } }
      limit: 4
      filter: { internal: { contentFilePath: { regex: "//content/projects/.+/" } } }
    ) {
      nodes {
        id
        excerpt
        fields {
          slug
        }
        frontmatter {
          title
          description
          featuredImage {
            childImageSharp {
              gatsbyImageData(width: 300)
            }
          }
          thumbnailImage {
            childImageSharp {
              gatsbyImageData(width: 300)
            }
          }
        }
      }
    }
  }
`;
